@import "../../global.scss";

.player-wrapper {
    padding-top: 80px;
    width: 100%;
    height: 800px;
    overflow: hidden;

        video {
            background-attachment: fixed;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            position: relative;
            overflow: hidden;
            width: 100%;
            height: 100%;
    
            // Use border radius to fix a bug on chrome @ https://bugs.chromium.org/p/chromium/issues/detail?id=400829#c31
            -webkit-border-radius: 1px;
            border-radius: 1px;
            z-index: 2;
            object-fit: cover;
        }
        &::before {
            content: '';
            position: absolute;
            height: inherit;
            width: 100%;
            background: $darkBackground; //Change as per your requirement
            z-index: 3;
            -webkit-clip-path: polygon(100% 79%, 0% 100%, 100% 100%);
            clip-path: polygon(100% 79%, 0% 100%, 100% 100%);
        }
    }

    .react-player {
        object-fit: cover;
        object-position: center;
        transform: translateY(-300px);
        overflow: hidden;
        z-index: -1;
        box-shadow: inset  10px 10px 10px 10px rgba(255, 0, 0, 0.35);

        video {
            position: relative;
            left: 0;
            top: 0;
            transform: none;
        }
    }

.about-section {
    background-color: $darkBackground;
    width: 100%;
    display: flex;
    justify-content: center;

    .about-container {
        width: 60%;
        h1 {
            font-weight: 300;
            font-size: 60px;
            float: right;
            color: $highlightColor;
            margin-right: 20px;
            border-bottom: 3px solid $highlightColor;
            margin-bottom: 30px;
        }

        p {
            font-weight: 400;
            font-size: 32px;
            float: left;
            color: $textColor;
        }
    }
}